/* eslint-disable no-unused-vars */
import axios from 'axios'
import Qs from 'qs'
import { isObject, isArray, isString } from './type'
import router from '../router'
const BASE_URL = ''

const Axios = axios.create({
  baseURL: BASE_URL,
  timeout: 30000,
  responseType: 'json',
  withCredentials: false,
  formData: false,
  isDownload: false
})

let Config = null

let loading = document.querySelector('#ajaxLoading')
loading.onclick = function () {
  this.style.display = 'none'
}

function startLoading () {
  if (!loading) return
  loading.style.display = 'block'
}

function endLoading () {
  if (!loading) return
  loading.style.display = 'none'
}

// 
Axios.interceptors.request.use(config => {
  const UID = sessionStorage.getItem('WXDYFS')
  const PATH = router.currentRoute.path
  // Message.closeAll()
  // if (!isString(UID) && PATH !=='/login') {
  //     Message.error('')
  //     router.push('/login')
  //     return
  // }

  let contentType = ''
  // POST
  if (config.method.toUpperCase() === 'POST') {
    if (config.formData) {
      contentType = 'application/x-www-form-urlencoded;charset=UTF-8'
      config.data = Qs.stringify(config.data)
    } else {
      contentType = 'application/json;charset=UTF-8'
      if (isObject(config.data) || isArray(config.data)) {
        config.data = JSON.stringify(config.data)
      }
    }
  }
  // GET
  if (config.method.toUpperCase() === 'GET') {
    contentType = 'application/json;charset=UTF-8'
  }
  if (contentType === '') {
    // Message.error('')
  }
  config.headers['Content-Type'] = contentType
  // 
  if (config.isDownload === true) {
    config.timeout = 120000
  }
  Config = config
  return config
}, error => {
  console.log(error, '')
  // Message.error(error && error.data.error)
  return Promise.reject(error.data.error)
})

